$(() => {
	if(!$('.accordion, .faq__item, .category__title').length)
		return false;

	$('.accordion').each((i, el) => {
		$(el).find('.accordion__content').hide()
	})

	$(document).on('click', '.accordion__top, .category__title', e => {
		let $this = $(e.currentTarget);

			$this
				.parent()
				.toggleClass('is-active')
				.find('.category__list, .accordion__content')
				.slideToggle(300);
	});


	$(document).on('click', '.category__title', e => {
		let $this = $(e.currentTarget);

		$this.parent().toggleClass('is-active');
	});

	$(document).on('click', '.faq__item', function(){
		if ( $(this).hasClass("active") ) {

			$(this).removeClass("active").find(".faq__content").slideUp(300);

		} else {

			$(".faq__item.active").removeClass("active").find(".faq__content").slideUp(300);

			$(this).addClass("active").find(".faq__content").slideDown(300);

		}

	});
})
$(() => {
	$(document).on('click', '.js-accordion-service', function(){
		if ($(this).hasClass("open")) {
			$(this).removeClass("open")
			$('#service-list').slideUp(300);
		} else {
			$(this).addClass("open");
			$('#service-list').slideDown(300);
		}
	});
})